import React  from 'react';
import '../styles/styles.css';
import 'animate.css';
import { useHistory } from 'react-router';


export const Pokedex = () => {
    
    const history = useHistory();

    const pokeballOpen = () =>{
        
        const red = document.getElementById('red');
        const white = document.getElementById('white');
        const center = document.getElementById('center')
            
        red.classList.add('animate__fadeOutUp');
        center.classList.add('animate__fadeOut');
        white.classList.add('animate__fadeOutDown');
     
        setTimeout(() => {
            history.push('/pokescreen');
        }, 700 );
    }
    


    return (
        <div className="d-flex flex-column login-container">
            
            <div id="red" className="parte-superior animate__animated animate_slow animate__fadeInDown">

                <h1 className="text-center text-white texto-superior animate__animated animate__slow animate__bounceInLeft" 
                > 
                    Welcome trainer, please press the pokeball button to start 
                </h1>

            </div>
                    
            <div 
                id="center"
                className="parte-central animate__animated  animate__fadeIn" 
            >   
                <div className="poke-button-outside animate__faster animate__fadeInDown">
                    
                    <button 
                        className="btn btn-light poke-button-center"
                        onClick={ pokeballOpen }
                    >
                    </button>
                </div>
            </div>


            <div id="white" className="parte-inferior animate__animated animate__fadeInUp">

                <h1 className="text-center text-black texto-inferior animate__animated animate__slow animate__bounceInRight" >
                    Bienvenido entrenador, por favor presiona el boton de la pokébola para empezar
                </h1>

            </div>
        
        </div>
    )
}
