
export const getPokemons = async( limit  = 12) => {

    const url = `http://localhost:5000/pokemons?_limit=${ limit }`
    const res = await fetch(url)
    const data = await res.json()

    const pokemons = data.map(  pokemon =>{
        return{
            id: pokemon.id,
            numPokedex: pokemon.numPokedex ,
            nombre: pokemon.nombre,
            tipo1: pokemon.tipo1,
            tipo2: pokemon.tipo2,
        }
    
    })

    return pokemons;

}
