import { useState, useEffect } from 'react';

import { getPokemons } from '../helper/getPokemons';


export const useFetchPokemon = ( limit ) => { 

    const [state, setState] = useState({
        data: [],
        loading: true
    });

    useEffect(() => {
        getPokemons( limit )
            .then( pokemon => {                
             setState({ 
                data: pokemon,
                loading: false      
            });
        })  
    
    }, [ limit ])
    return state;
}
   
    