import React, { useState }  from 'react';
import { Navbar } from './Navbar';
import { PokeCard } from './PokeCard';
import { useFetchPokemon } from '../hooks/useFetchPokemon';

export const PokemonScreen = () => {
   
    const [newLimit, setNewLimit] = useState(24)
    const { data:pokemon } = useFetchPokemon( newLimit );
    

    const handleLoadMore = () =>{
        setNewLimit( newLimit + 25 )
    }

    return (
        <div className="pokescreen-container">

            <Navbar />


           <h1 className="text-center mt-3"> Pokémons </h1>
           
           <hr />
            
            
            {
                <div className=" d-flex flex-wrap justify-content-center animate__animated animate__fadeIn">

                    { 
                        pokemon.map( pokemons  => (
                            <PokeCard
                                key={pokemons.id}
                                {...pokemons }
                            />
                        ))

                    }
                </div> 
            }
            
            <button 
                className="btn btn-primary w-50 align-text-center"
                onClick={handleLoadMore}
            >
                    Load More
            </button>

        </div>
    )
}
