import React from 'react'
import { Link, useHistory } from 'react-router-dom'
import Logo from '../images/Logo.png'



export const Navbar = () => {
   
    const history = useHistory()

    const handleOut = () =>{
        history.push('/')
    }

    return (
        <nav className="d-flex navbar navbar-expand-sm bg-danger text-white">
                <Link 
                    className=" flex-grow-1 navbar-brand"  
                    to="/"
                >
                    <img className="logo" src={ Logo }  alt="logo" /> 
                </Link>
            
            
            
            <div className="outButtonContainer ">
                
                <button
                    className="nav-item nav-link btn outButton"                        
                    onClick={ handleOut }
                >
                    <i className="fas fa-sign-out-alt"></i> Close Pokedex
                </button>    
                
            </div>      
        


        </nav>
    )
}

